<template>
  <el-container >  
    <el-header>
     <div class="onlyforchrome" style="display:block;font-weight:bold; font-size:30px;text-align:center;color:#777E8C;"><span style="color:#6cf">T-</span><span style="color:#ff6680">Rex</span> Runner</div>

   
    </el-header>
      <el-main>
         <router-view></router-view>
      </el-main>
    <!--     <el-footer style="background-color: #333;">
         
        </el-footer> -->
  </el-container>  

</template>

<script>
import 'assets/fonts/iconfont'
export default {
  data() {
    return {}
  },
  computed: {
    allRouters() {}
  },
  created() {},
  watch: {
    $route(to, from) {}
  },
  methods: {
    handleOpen(key, keyPath) {}
  }
}
</script>

<style lang="less">
.el-main {
  padding: 0;
}

html,
body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

td {
  padding: 10px;
}

.icon {
  -webkit-user-select: none;
  user-select: none;
  display: inline-block;
}

.icon-offline {
  content: -webkit-image-set(
    url(~assets/images/default_100_percent/100-error-offline.png) 1x,
    url(~assets/images/default_200_percent/200-error-offline.png) 2x
  );
  position: relative;
}

.hidden {
  display: none;
}

/* Offline page */

.offline .interstitial-wrapper {
  color: #2b2b2b;
  font-size: 1em;
  line-height: 1.55;
  margin: 0 auto;
  max-width: 600px;
  padding-top: 210px;
  width: 100%;
}

.offline .runner-container {
  height: 150px;
  max-width: 600px;
  overflow: hidden;
  position: absolute;
  top: 165px;
  width: 44px;
}

.offline .runner-canvas {
  height: 150px;
  max-width: 600px;
  opacity: 1;
  overflow: hidden;
  position: absolute;
  top: 0;
  z-index: 2;
}

.offline .controller {
  background: rgba(247, 247, 247, 0.1);
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100vw;
  z-index: 1;
}

#offline-resources {
  display: none;
}

@media (max-width: 420px) {
  .suggested-left > #control-buttons,
  .suggested-right > #control-buttons {
    float: none;
  }
  .snackbar {
    left: 0;
    bottom: 0;
    width: 100%;
    border-radius: 0;
  }
}

@media (max-height: 350px) {
  h1 {
    margin: 0 0 15px;
  }
  .icon-offline {
    margin: 0 0 10px;
  }
  .interstitial-wrapper {
    margin-top: 5%;
  }
  .nav-wrapper {
    margin-top: 30px;
  }
}

@media (min-width: 600px) and (max-width: 736px) and (orientation: landscape) {
  .offline .interstitial-wrapper {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (min-width: 420px) and (max-width: 736px) and (min-height: 240px) and (max-height: 420px) and (orientation: landscape) {
  .interstitial-wrapper {
    margin-bottom: 100px;
  }
}

@media (min-height: 240px) and (orientation: landscape) {
  .offline .interstitial-wrapper {
    margin-bottom: 90px;
  }
  .icon-offline {
    margin-bottom: 20px;
  }
}

@media (max-height: 320px) and (orientation: landscape) {
  .icon-offline {
    margin-bottom: 0;
  }
  .offline .runner-container {
    top: 10px;
  }
}

@media (max-width: 240px) {
  .interstitial-wrapper {
    overflow: inherit;
    padding: 0 8px;
  }
}

button.active {
  background: black;
  color: white;
}

button {
  width: 100%;
  height: 55px;
  background: white;
  border: 3px solid black;
  margin-right: 10px;
  cursor: pointer;
}

#noExtension {
  padding-top: 200px;
  text-align: center;
  font-size: 22px;
  position: absolute;
  width: 100%;
  display: none;
  z-index: 999999999;
  background-color: #fff;
  border: 1px solid #000;
  width: 100%;
  height: 200px;
  line-height: 200px;
}
</style>



