import Vue from 'vue'
const _import = file => require('pages/' + file + '.vue')

const root = Vue.component('root', {
  template: '<router-view></router-view>'
})

let routes = [
  {
    path: '/',
    component: root,
    name: '主页',
    children: [
      {
        path: 'test',
        component: _import('test'),
        name: '测试专用'
      },
      {
        path: '',
        component: _import('index'),
        name: '测试专用'
      }
    ]
  },
  {
    path: '*',
    redirect: { path: '/404' }
  }
]

export default routes



