<template lang="html">
 <el-main>
   <div class="onlyforchrome" style="margin-top:5px;text-align:center;color:#8a8a8a;margin-bottom:80px; font-size:1em;line-height:1.6em;">
        <br> <span id="firsttimeinfo">按 空格 或 向上箭头 可以跳跃,按 向下箭头 可以低头前进.</span></div>
    <div id="main-frame-error" class="interstitial-wrapper">
        <div id="main-content">
            <div class="icon icon-offline" alt=""></div>
        </div>
         <el-button type="primary" @click="uploadScore" :plain="true"><i class="el-icon-upload2 el-icon--left"></i>提交分数<i class="el-icon-upload2 el-icon--right"></i></el-button>
        <div id="offline-resources">
            <img id="offline-resources-1x" src="~assets/images/default_100_percent/100-offline-sprite.png">
            <img id="offline-resources-2x" src="~assets/images/default_200_percent/200-offline-sprite.png">
            <template id="audio-resources">
                <audio id="offline-sound-press" src="data:audio/mpeg;base64,"></audio>
                <audio id="offline-sound-hit" src="data:audio/mpeg;base64,"></audio>
                <audio id="offline-sound-reached" src="data:audio/mpeg;base64,"></audio>
            </template>
        </div>
    </div>
    <div style="position: fixed;top: 2rem;left: 2rem;font-size: 1rem;height: 90%;" class="hidewhenplaying" id="app" >
      <el-table
      :data="list"
       max-height="550"
      style="width: 380px">
       <el-table-column
      type="index"
        label="排名"
      width="50">
    </el-table-column>
     <el-table-column
        label="分数">
         <template slot-scope="scope">
        <span style="margin-left: 10px">{{ Number(scope.row.value.split(':')[0]) }}</span>
      </template>
      </el-table-column>
      <el-table-column
        label="玩家"
        width="360">
         <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.value.split(':')[1] }}</span>
      </template>
      </el-table-column>
     
    </el-table>
    </div>
      </el-main>
</template>

<script>
import NebPay from 'libs/nebpay'
const nebPay = new NebPay()
const dappAddress = 'n1z1hDSBbFxSG4DAvJ8qhwPAV2cR6a1uvCe'
export default {
  mounted() {
    setTimeout(() => {
      if (typeof webExtensionWallet === 'undefined') {
        this.$notify.error({
          title: '提示',
          duration: 0,
          dangerouslyUseHTMLString: true,
          message:
            '请先安装<a target="_blank" style="color: blue;" href="https://github.com/ChengOrangeJu/WebExtensionWallet">WebExtensionWallet</a> 再进行游戏'
        })
      }
    }, 0)

    setInterval(() => {
      this.getScoreList(0)
    }, 15000)
    this.getScoreList(0)
  },
  data() {
    return {
      initKey: '',
      list: []
    }
  },
  methods: {
    uploadScore() {
      if (!window.cscr) {
        this.$message.error('分数不能为空！')
        return
      }

      var value = '0'

      var callArgs = []
      callArgs.push(this.initKey)
      callArgs.push(window.cscr)
      nebPay.call(dappAddress, value, 'setScore', JSON.stringify(callArgs), {
        //使用nebpay的call接口去调用合约,
        listener: resp => {
          console.log('response of push: ' + resp)
        }
      })
    },
    getScoreList(page) {
      this.initKey = 'wish:' + new Date().getTime() + this.RndNum(3)
      var value = '0'
      var callArgs = '[10,' + page * 10 + ']' //in the form of ["args"]
      nebPay.simulateCall(dappAddress, value, 'scoreList', callArgs, {
        //使用nebpay的simulateCall接口去执行get查询, 模拟执行.不发送交易,不上链
        listener: resp => {
          if (!resp.result) return
          var result = eval(JSON.parse(resp.result))
          if (result !== 'null') {
            this.list = result
          }
        }
      })
    },
    RndNum(n) {
      var rnd = ''
      for (var i = 0; i < n; i++) rnd += Math.floor(Math.random() * 10)
      return rnd
    }
  }
}
</script>
<style lang="less" scoped>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: '';
}

.clearfix:after {
  clear: both;
}
</style>



